.header{
    display:flex;
    justify-content: space-between;
    border: solid black;
}

.logo{
    width: 150px;
    margin-top: 0.0100px;
}

.nav-items >ul{
    display: flex;
    margin-left: 700px;
    list-style-type: none;
    padding-top:5px ;

}

.nav-items>ul>li{
    padding:10px;
    margin: 10px;
} 
.nav-items>ul>li:hover{
    cursor: pointer;
    text-decoration: underline;
}

.cart{
    width: 50px;

}


.cart:hover{
    cursor: pointer;
}

.res-cont{
    display: flex;
    flex-wrap: wrap;
    padding-left: 60px;
    width: 100%;
}


.res-card{
    width: 250px;
    height: 350px;
    margin: 20px;
    padding-left: 10px;
}

.res-card:hover{
    cursor: pointer;
    border: 1px solid black;
}

.search{
    padding: 10px;
}

.res-name{
    text-align: center;
    margin-right: 10px;
    margin-top: 1px;
}

.noodles{
    width: 230px;
    height: 138px;
    padding: 8px;
}